.wrapper {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 1;
}

.logo {
  display: flex;
  height: 92px;
  align-items: center;
  margin-top: 10px;
  padding-left: 22px;
}
